import { Component, h, Element, Prop } from '@stencil/core';
import ApexCharts from 'apexcharts';

@Component({
  tag: 'line-chart',
  styleUrl: 'line-chart.css',
  shadow: true,
})
export class LineChart {
  @Element() el!: HTMLElement;

  @Prop() categories: string[] = ['Jan', 'Feb', 'Mar', 'Apr'];
  @Prop() series: { name: string; data: number[] }[] = [
    { name: 'Series 1', data: [10, 20, 15, 30] },
  ];

  private chart?: ApexCharts;

  componentDidLoad() {
    const target = this.el.shadowRoot?.querySelector('#chart');
    if (target) {
      const options = {
        chart: {
          type: 'line',
          height: 350,
        },
        xaxis: {
          categories: this.categories,
        },
        yaxis: {
          labels: {
            show: true,
          },
        },
        series: this.series,
      };
      this.chart = new ApexCharts(target, options);
      this.chart.render();
    }
  }

  disconnectedCallback() {
    this.chart?.destroy();
  }

  render() {
    return (
      <div>
        <ix-style-loader></ix-style-loader>

        <div id="chart">

        </div>
      </div>
    );
  }
}
